import React from "react";
import Button from "./buttons";
import "../styles/main.css";

class Calculator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      display: "",
      current: "",
      answer: "",
    };
    this.handleNumbers = this.handleNumbers.bind(this);
    this.handleDisplay = this.handleDisplay.bind(this);
    this.clearDisplay = this.clearDisplay.bind(this);
    this.evaluate = this.evaluate.bind(this);
    this.formula = this.formula.bind(this);
    this.delete = this.delete.bind(this);
  }
  handleNumbers(e) {
    this.setState((state) => {
      return {
        display: this.handleDisplay(e.target.value, state.display),
      };
    });
  }
  handleDisplay(inputStr, stateStr) {
    let strRegex = /[+/*-]$/;
    let inputRegex = /[+/*]/;
    if (stateStr.length > 21) {
      this.setState({ answer: "LIMIT", current: this.state.answer });
      // setTimeout(() => this.setState({ answer: "0" }), 1000);
      return stateStr;
    }
    if (strRegex.test(stateStr) && inputRegex.test(inputStr)) {
      if (/[+/*]-$/.test(stateStr)) {
        return stateStr.replace(/[+/*]-$/, inputStr);
      } else {
        // console.log("in else");
        return stateStr.replace(/[+/*-]$/, inputStr);
      }
    } else if (/--$|([+*/]-$)/.test(stateStr) && /-/.test(inputStr)) {
      return stateStr;
    } else if (/\./.test(inputStr)) {
      if (/(\.\d*)$/.test(stateStr)) {
        return stateStr;
      } else if (/\D$|(^$)/.test(stateStr)) {
        return stateStr + "0" + inputStr;
      }
      return stateStr + inputStr;
    }
    return stateStr + inputStr;
  }
  clearDisplay() {
    this.setState({ display: "", answer: 0 });
  }
  evaluate() {
    // console.log("evaluation time");
    this.setState((state) => {
      return {
        display: state.display.replace(/[+-/*]$/, ""),
      };
    });
    if (/^[*/]/.test(this.state.display)) {
      // console.log("here");
      return;
    }
    let regExp =
      /\d+(\.\d+)?|((^-\d+)(\.\d+)?)|((?<=[+/*-])-\d+(\.\d+)|^\+\d+)/g;
    let numberArr = (this.state.display.match(regExp) || []).map((val) =>
      Number.parseFloat(val)
    );
    // console.log(numberArr);
    let operators = this.state.display.match(/(?<=\d)[-*/+]/g) || [];
    // console.log(operators);

    this.setState({ answer: this.formula(numberArr, operators) });
    // console.log(this.formula(numberArr, operators));
  }
  formula(arr, operators) {
    let arr2 = [...arr];
    for (let i = 0; i < arr.length; i++) {
      let o;
      if (arr2.length < 2) {
        if (
          arr2[0].toString().includes(".") &&
          arr2[0].toString().split(".")[1] > 6
        ) {
          return arr2[0].toPrecision(9);
        } else {
          return arr2[0];
        }
      }
      if (operators[i] === "+") {
        o = arr2[0] + arr2[1];
        arr2.splice(0, 2, o);
      }
      if (operators[i] === "-") {
        o = arr2[0] - arr2[1];
        arr2.splice(0, 2, o);
      }
      if (operators[i] === "*") {
        o = arr2[0] * arr2[1];
        arr2.splice(0, 2, o);
      }
      if (operators[i] === "/") {
        o = arr2[0] / arr2[1];
        arr2.splice(0, 2, o);
      }
    }
  }
  delete() {
    this.setState((state) => {
      return { display: state.display.replace(/.$/, "") };
    });
  }
  render() {
    return (
      <div className="calculator">
        <p className="calculator-entries display ">{this.state.display}</p>
        <p className="calculator-answer display">{this.state.answer}</p>
        <Button
          handleNumbers={this.handleNumbers}
          clearDisplay={this.clearDisplay}
          evaluate={this.evaluate}
          delete={this.delete}
        />
      </div>
    );
  }
}

export default Calculator;
