import React from "react";
import "../styles/button.css";

class Button extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container-btn">
        <button
          value="/"
          className="btn operators"
          onClick={this.props.handleNumbers}
        >
          /
        </button>
        <button
          value="*"
          className="btn operators"
          onClick={this.props.handleNumbers}
        >
          X
        </button>
        <button onClick={this.props.clearDisplay} className="btn power">
          AC
        </button>
        <div className="pad">
          <button
            value="1"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            1
          </button>
          <button
            value="2"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            2
          </button>
          <button
            value="3"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            3
          </button>
          <button
            value="4"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            4
          </button>
          <button
            value="5"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            5
          </button>
          <button
            value="6"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            6
          </button>
          <button
            value="7"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            7
          </button>
          <button
            value="8"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            8
          </button>
          <button
            value="9"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            9
          </button>
          <button
            value="0"
            className="btn number"
            onClick={this.props.handleNumbers}
          >
            0
          </button>
          <button
            value="."
            className="btn fullStop number"
            onClick={this.props.handleNumbers}
          >
            .
          </button>
        </div>
        <button
          value="+"
          className="btn operators"
          onClick={this.props.handleNumbers}
        >
          +
        </button>
        <button
          value="-"
          className="btn operators"
          onClick={this.props.handleNumbers}
        >
          -
        </button>

        <button value="*" className="btn del" onClick={this.props.delete}>
          Del
        </button>

        <button onClick={this.props.evaluate} className="btn eval">
          =
        </button>
      </div>
    );
  }
}

export default Button;
